<template>
  <div class="particulars">
    <div class="par-heard">
      <div class="hread">
        <van-nav-bar left-text="返回" :border="false" size="20" left-arrow @click-left="back">
          <van-icon name="share" slot="right" size="20" color="black" />
        </van-nav-bar>
      </div>
    </div>
    <!-- 书籍内容区 -->
    <div class="par-fiction">
      <div class="fiction-text" :id="this.partioc.Id">
        <!-- 信息区 -->
        <div class="par-message">
          <div class="message-heard">
            <div class="mesg-fl">
              <img :src="this.partioc.Img" class="max-img" />
            </div>
            <div class="mesg-fr">
              <div class="mesg-title">
                <h3>{{this.partioc.Name}}</h3>
                <p>{{this.partioc.Author}}</p>
                <p>
                  小说
                  <span>·</span>
                  {{this.partioc.CName}}
                  <span>·</span>电子竞技
                </p>
              </div>
              <div class="mesg-grade">
                <div class="grade-1">
                  <span>{{this.title}}</span>
                  <van-rate
                    v-model="value"
                    color="#F1B65A"
                    void-icon="star"
                    size="15"
                    void-color="#BABABA"
                    readonly
                  />
                </div>
                <div class="grade-2">
                  <span>5.4万人评分</span>
                </div>
              </div>
            </div>
          </div>
          <div class="message-show">
            <ul>
              <li>
                <p class="p1">
                  <span>534</span>万字
                </p>
                <p class="p2">{{this.partioc.BookStatus}}</p>
              </li>
              <li>
                <p class="p1">
                  <span>534</span>名
                </p>
                <p class="p2">霸王票</p>
              </li>
              <li>
                <p class="p1">
                  <span>6.4</span>万瓶
                </p>
                <p class="p2">灌溉</p>
              </li>
            </ul>
          </div>
          <div class="book-slidow">
            <div class="slidow" :style="{display}">{{this.partioc.Desc}}</div>
            <div class="back-lt">
              <van-icon :name="iconname" color="#b5b5b5" @click="sidow(display)" />
            </div>
          </div>
          <div class="book-catalogue" @click="section({name: 'section', params:{id:toID}})">
            <span class="text-t">书籍目录</span>
            <div class="cata-fr">
              <span>
                <strong>·</strong>
                {{this.partioc.LastChapter}}
              </span>
              <van-icon name="arrow" size="20" color="#b5b5b5" />
            </div>
          </div>
        </div>
        <!-- 评论区 -->
        <div class="par-comment">
          <div class="comment-title">
            <div class="com-fl">
              <span>其他推荐作品</span>
              <span>{{this.index}}部作品</span>
            </div>
          </div>
          <div class="com-img">
            <ul>
              <li
                class="animated heartBeat"
                v-for="(item,index) in this.partioc.SameCategoryBooks"
                :key="index"
                :id="item.Id"
              >
                <div class="com-logo">
                  <img
                    class="max-img"
                    :src="'https://imgapi.jiaston.com/BookFiles/BookImages/'+item.Img"
                    loading="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1576141964201&di=b65f41ae2c0efca1c382dec9882b63ed&imgtype=0&src=http%3A%2F%2Fhbimg.huabanimg.com%2F23907369b8a233f4b426d739875621c6b0fc248a1d28c-8V4M0p_fw658"
                    v-lazy="'https://imgapi.jiaston.com/BookFiles/BookImages/'+item.Img"
                  />
                </div>
                <p>{{item.Name}}</p>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>

    <!-- 点击阅读 -->
    <div class="book-read">
      <div class="read-1">
        <div class="red-sc">
          <van-icon
            :name="iconn"
            :color="iconcolor"
            size="22"
            @click="join(iconn,show)"
            :v-model="show"
          />
          <span>{{joinbookrack}}</span>
        </div>
      </div>
      <div class="read-2" @click="torea({name: 'interfacered', params:{id:id}})">立即阅读</div>
      <div class="read-1">
        <div class="red-sc">
          <van-icon name="down" size="22" />
          <span>下载全本</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      value: null,
      title: "",
      display: "-webkit-box",
      iconname: "arrow-down",
      partioc: "",
      index: 0,
      iconn: "like-o",
      iconcolor: "black",
      id: null,
      toID: null,
      show: false,
      joinbookrack: "加入书架",
      imgUrl: "https://imgapi.jiaston.com/BookFiles/BookImages/"
    };
  },
  computed: {},
  methods: {
    sidow(display) {
      this.display = display == "block" ? "-webkit-box" : "block";
      this.iconname = display == "block" ? "arrow-down" : "arrow-up";
    },
    torea(o) {
      this.$router.push(o);
    },
    section(o) {
      this.$router.push(o);
    },
    back() {
      this.$router.go(-1);
    },
    //加入书架
    join(iconn, show) {
      //收藏图标状态
      this.iconn = iconn == "like" ? "like-o" : "like";
      this.iconcolor = iconn == "like" ? "black" : "red";
      this.show = show == false ? true : false;
      //获取用户登录状态
      let user = localStorage.getItem("user");
      //用户是否存在
      if (user) {
        user = JSON.parse(user);
        //  不存在则提示登录
        if (!user.isLogin) {
          this.$toast({ message: "请先去登录" });
          return this.$router.push({ name: "register" });
        }
        //获取收藏的书
        let likebooks = localStorage.getItem("likebooks");
        likebooks = likebooks ? JSON.parse(likebooks) : {};

        let currentLibook = likebooks[user.username];
        if (!currentLibook) {
          likebooks[user.username] = [];
        }

        // for (let i = 0; i < likebooks[user.username].length; i++) {
        //   if (likebooks[user.username][i].Id == this.partioc.Id) {
        //     this.$toast({ message: "本书已在书架" });
        //     return;
        //   }
        // }
        this.partioc.show = this.show;
        if (this.show) {
          for (let i = 0; i < likebooks[user.username].length; i++) {
            if (likebooks[user.username][i].Id == this.partioc.Id) {
              //如果小说相同,则阻断
              return;
            }
          }
          likebooks[user.username].unshift(this.partioc);
          localStorage.setItem("likebooks", JSON.stringify(likebooks));
          this.joinbookrack = "已加书架";
          this.$toast({ message: "已加入书架" });
        } else {
          for (let i = 0; i < likebooks[user.username].length; i++) {
            if (likebooks[user.username][i].Id == this.partioc.Id) {
              likebooks[user.username].splice(i, 1);
            }
          }
          localStorage.setItem("likebooks", JSON.stringify(likebooks));
          this.joinbookrack = "加入书架";
          this.$toast({ message: "已取消加入书架" });
        }
      } else {
        this.$toast({ message: "请登录" });
        this.$router.push({ name: "register" });
      }
    }
  },
  mounted() {
    this.partioc = JSON.parse(localStorage.getItem("book-info"));
    this.value = this.partioc.BookVote.Score / 2;
    this.title = this.partioc.BookVote.Score;
  },
  created() {
    this.partioc = JSON.parse(localStorage.getItem("book-info"));
    //获取ID
    let id = this.$route.params.id;
    this.id = id;
    //判断如果用户的书架存在这本书,则显示用户储存的书籍信息,反之则显示公共的书籍信息
    //获取用户登录状态
    let user = JSON.parse(localStorage.getItem("user"));

    //获取收藏的书
    let likebooks = localStorage.getItem("likebooks");
    likebooks = likebooks ? JSON.parse(likebooks) : {};
    let currentLibook = likebooks[user.username];
    this.$axios({
      methods: "GET",
      dataType: "jsonp",
      url: "http://localhost:1000/getjson/xiaoshuo-info1"
    }).then(reche => {
      reche.data.forEach(v => {
        v.data.Img = this.imgUrl + v.data.Img;
        if (id == v.data.Id) {
          //判断喜欢的书籍数据是否存在
          if (likebooks) {
            for (let i = 0; i < currentLibook.length; i++) {
              //如果存在显示用户的书籍信息
              if (currentLibook[i].Id == v.data.Id) {
                this.partioc = currentLibook[i];
                this.iconn = "like";
                this.iconcolor = "red";
                this.show = true;
                this.joinbookrack = "已加书架";
                return localStorage.setItem(
                  "book-info",
                  JSON.stringify(currentLibook[i])
                );
              }
            }
          }
          //不存在显示公共信息
          this.partioc = v.data;
          this.toID = v.data.Id;
          localStorage.setItem("book-info", JSON.stringify(v.data));
          this.value = v.data.BookVote.Score / 2;
          this.title = v.data.BookVote.Score;
        }
      });
      this.partioc.SameCategoryBooks.forEach(v => {
        this.index += 1;
      });
      // 查找id
    });
    this.toID = this.partioc.Id;
  }
};
</script>
<style lang="less">
p {
  margin: 0;
}
.max-img {
  width: 100%;
  height: 100%;
  display: block;
}
.particulars {
  overflow: hidden;
  .par-heard {
    width: 100%;
    overflow: hidden;
    position: sticky;
    left: 0;
    top: 0;

    z-index: 10;
    .bad {
      width: 100%;
      height: 24px;
    }
    .hread {
      .van-nav-bar__left {
        .van-icon {
          color: black;
        }
        .van-nav-bar__text {
          color: black;
        }
      }
    }
  }
  .par-fiction {
    width: 100%;
    overflow-y: auto;
    height: calc(667px - 46px - 50px);
    .fiction-text {
      width: calc(100% - 30px);
      margin: 0 auto;
      background: #f8f8f8;
      .par-message {
        width: 100%;
        overflow: hidden;
        background: white;
        .message-heard {
          overflow: hidden;
          .mesg-fl {
            float: left;
            width: 90px;
            height: 120px;
          }
          .mesg-fr {
            float: left;
            width: 240px;
            margin-left: 15px;
            .mesg-title {
              float: left;
              h3 {
                margin: 0;
                font-size: 16px;
              }
              span {
                margin: 0 4px;
              }
              p:nth-child(2) {
                color: #72d5a7;
                font-size: 13px;
                margin-top: 10px;
              }
              p:nth-child(3) {
                margin-top: 5px;
                font-size: 12px;
                color: #b5b5b5;
              }
            }
            .mesg-grade {
              overflow: hidden;
              float: left;
              margin-top: 15px;
              .grade-1 {
                span {
                  font-size: 14px;
                  margin-right: 3px;
                }
              }
              .grade-2 {
                span {
                  color: #d9d9d9;
                }
              }
            }
          }
        }
        .message-show {
          width: 100%;
          overflow: hidden;
          margin-top: 20px;
          ul {
            overflow: hidden;
            li:nth-child(1) {
              margin-left: 0;
            }
            li:nth-child(2) {
              margin-left: 100px;
            }
            li:nth-child(3) {
              float: right;
            }
            li {
              float: left;
              max-width: 80px;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              font-size: 13px;
              p {
                text-align: center;
              }
              .p1 {
                width: 100%;
                span {
                  font-size: 16px;
                  font-weight: 600;
                }
              }
              .p2 {
                margin-top: 3px;
                color: #b5b5b5;
              }
            }
          }
        }
        .book-slidow {
          margin-top: 20px;
          border-bottom: 1px solid #ededed;
          .slidow {
            font-size: 13px;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            overflow: hidden;
            line-height: 15px;
            color: #b5b5b5;
          }
          .back-lt {
            text-align: right;
          }
        }
        .book-catalogue {
          padding: 10px 0;
          width: 100%;
          overflow: hidden;
          .text-t {
            float: left;
            font-size: 16px;
            font-weight: 600;
          }
          .cata-fr {
            float: right;
            span {
              font-size: 13px;
              color: #b5b5b5;
              float: left;
              margin-right: 5px;
              strong {
                margin: 0 5px;
              }
            }
          }
        }
      }
      .par-comment {
        margin-top: 10px;
        background: white;
        width: 100%;
        overflow: hidden;
        .comment-title {
          width: 100%;
          overflow: hidden;

          line-height: 40px;
          .com-fl {
            float: left;
            span:nth-child(1) {
              font-size: 16px;
              font-weight: 600;
            }
            span:nth-child(2) {
              margin-left: 5px;
              color: #b5b5b5;
              font-size: 12px;
            }
          }
          .com-fr {
            float: right;
            font-size: 13px;
            span {
              color: #64d2a1;
              margin-left: 3px;
            }
          }
        }
        .com-img {
          width: 100%;
          overflow: hidden;
          ul {
            overflow: hidden;
            li {
              float: left;
              width: 80px;
              margin-left: 5px;
              margin-bottom: 5px;
              .com-logo {
                width: 80px;
                height: 80px;
              }
              p {
                margin-top: 5px;
                margin-bottom: 5px;
                text-align: center;
                max-width: 100%;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
              }
            }
          }
        }
      }
    }
  }
  .book-read {
    width: 100%;
    position: fixed;
    left: 0;
    bottom: 0;
    overflow: hidden;
    font-size: 13px;
    line-height: 50px;
    .read-1 {
      width: 30%;
      float: left;
      text-align: center;
      border-top: 1px solid #f1f1f1;
      .red-sc {
        overflow: hidden;
      }
      span {
        margin-left: 5px;
      }
    }
    .read-2 {
      float: left;
      width: 40%;
      text-align: center;
      font-size: 13px;
      color: white;
      background: #07c160;
    }
  }
}
</style>